                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button size="small" @click="toggleCreateDialog()"> </el-button>
                    <el-button size="small" v-if="isCreate" type="primary" @click="createDevices()"> </el-button>
                    <el-button size="small" v-else type="success" @click="editDevices()"> </el-button>
                </div>
