                    :page-size="default_limit"
                    layout="total, sizes,prev, pager, next, jumper"
                    :total="totalSubTable">
            </el-pagination>
        </div>

        <!---->
        <el-dialog title="" :visible.sync="showDownload" width="70%" class="downLoadDialog">
            <el-form :model="downloadForm" label-width="100px">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="ID">
                            <el-input v-model.trim="downloadForm.dev_id"
                                      placeholder="ID" clearable size="mini"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="ID">
                            <el-input v-model.trim="downloadForm.trans_id"
                                      placeholder="ID" clearable size="mini"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-date-picker
                                    style="width: 100%;"
                                    size="mini"
                                    value-format="yyyy-MM-dd"
                                    v-model="downloadForm.start_date"
                                    type="date">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-date-picker
                                    style="width: 100%;"
                                    size="mini"
                                    value-format="yyyy-MM-dd"
                                    v-model="down_end_date"
                                    type="date">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-select v-model="downloadForm.status" clearable size="mini">
                                <el-option
                                        v-for="item in statusList"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="showDownload = false"> </el-button>
                <el-button type="primary" @click="startDownload()"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
  name: "bills",
  data() {
    return {
      table_height: 500,
      totalSubTable: 0,
      default_limit: 50,
      new_start: 0,
      mainForm: {
        dev_id: "",
        trans_id: "",
        status: "",
        start_date: "",
        end_date: "",
        is_imported: "",
        start: "",
        limit: ""
      },
      statusList: [
        {
          label: "",
          value: "100"
        },
        {
          label: "",
          value: "101"
        },
        {
          label: "",
          value: "102"
        },
        {
          label: "",
          value: "103"
        },
        {
          label: "",
          value: "104"
        },
        {
          label: "",
          value: "105"
        },
        {
          label: "",
          value: "107"
        },
        {
          label: "",
          value: "109"
        },
        {
          label: "",
          value: "110"
        }
      ],
      subTable: [],
      multipleSelection: [], //  
      currentPage: 1,
      id_arr: [],
      showDownload: false,
      down_end_date: "",
      search_end_date: "",
      fail_id_arr: [],
      downloadForm: {
        dev_id: "",
        trans_id: "",
        start_date: "",
        end_date: "",
        status: "100"
      }
    };
  },
  mounted() {
    this.setDefaultDate();
    this.searchOrder();
    this.$nextTick(() => {
      let autoHeight =
        document.documentElement.clientHeight -
        this.$refs.btns.offsetHeight -
        this.$refs.mainForm.offsetHeight -
        this.$refs.pagination.offsetHeight -
        64;
      this.table_height = autoHeight;
    });
  },
  methods: {
      tableRowClassName({row, rowIndex}) {
          if (this.fail_id_arr.includes(row.id)) {
              return 'warning-row';
          }
          return '';
      },
      setDefaultDate() {
      // 1
      let myDate = new Date();
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let date = myDate.getDate() + 1;
      let sTime = `${year}-${month}-${date - 1}`;
      this.mainForm.start_date = `${year}-${month}-1`;
      this.downloadForm.start_date = sTime;
      // 
      this.down_end_date = `${year}-${month}-${date - 1}`;
      this.search_end_date = `${year}-${month}-${date - 1}`;
    },
    startDownload() {
      this.showDownload = false;
      this.downloadOrder();
    },
    handleSizeChange(val) {
      this.default_limit = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val || this.currentPage;
      this.new_start = (this.currentPage - 1) * this.default_limit;
      this.searchOrder(false);
    },
    handleSelectionChange(val) {
      // 
      this.multipleSelection = val;
    },
    getSelectedID() {
      if (!this.multipleSelection.length) {
        alert("");
        return false;
      }
      this.id_arr = [];
      this.multipleSelection.forEach(val => {
        this.id_arr.push(val.id);
      });
      return true;
    },
    downloadOrder(page = 1) {
      let params = JSON.parse(JSON.stringify(this.downloadForm));
      params.page = page;
      params.per_page = 50;
      params.end_date = this.down_end_date;
      if (!this.checkDateDuration(params.start_date, this.down_end_date))
        return;
      this.$axios({
        url: `/api/orders?action=get_orders`,
        params: params
      }).then(res => {
        if (res.state === 1) {
          if (res.page) {
            this.downloadOrder(page++);
          }
          if (res.page == null) {
            this.$notify({
              title: "",
              type: "success"
            });
          }
          return;
        }
        alert(res.errmsg);
      });
    },
    deleteOrder() {
      // 
      this.ajaxFunction(null, "DELETE", "", "/api/orders/");
    },
    resetStatus() {
      // 
      this.ajaxFunction("init_order", "GET", "");
    },
    importC8() {
      // c8
      this.ajaxFunction("import", "GET", "C8");
    },
    async ajaxFunction(action, method, text, url = "") {
      if (!this.getSelectedID()) return;
      let count_failed = 0;
      let count_succeed = 0;
      let _total = 0;
      this.fail_id_arr = [];
      for await (let id of this.id_arr) {
        this.$axios({
          method: method,
          url: url ? url + id : `/api/orders/${id}?action=${action}`
        })
          .then(res => {
            if (res.state === 1) {
              count_succeed++;
              return;
            }
            count_failed++;
            this.fail_id_arr.push(id)
            for (let j = 0, len2 = this.subTable.length; j <len2; j++) { // Title
              if (this.subTable[j].id === id) {
                document.getElementsByClassName('el-table__row')[j].setAttribute('title', res.errmsg)
              }
            }
          })
          .finally(() => {
            _total++;
            if (_total === this.id_arr.length) {
              alert(
                `${this.id_arr.length}${text}${count_succeed}${count_failed}`
              );
            }
          });
      }
      await this.handleCurrentChange(1);
      this.multipleSelection = [];
    },
    addDate(end_date) {
          var _end_date = new Date(end_date);
          var millSeconds = Math.abs(_end_date) + 24 * 60 * 60 * 1000;
          var added = new Date(millSeconds);
          let year = added.getFullYear();
          let month = added.getMonth() + 1;
          let date = added.getDate();
          let eTime = `${year}-${month}-${date}`;
          return eTime;
    },
    checkDateDuration(start, end) {
      if (!start || !end) {
        alert("7");
        return false;
      }
      if (
        (new Date(end + " 00:00:00") - new Date(start + " 00:00:00")) /
          (1000 * 60 * 60 * 24) >=
        7
      ) {
        alert("7");
        return false;
      }
      return true;
    },
    searchOrder(bool = true) {
      // 
      this.mainForm.limit = this.default_limit;
      this.mainForm.start = this.new_start;
      let params = JSON.parse(JSON.stringify(this.mainForm));
      params.end_date = this.search_end_date? this.addDate(this.search_end_date) : "";
      if (Date.parse(params.end_date) - Date.parse(params.start_date) < 0) {
        // end 1970-1-1
        alert("");
        return;
      }
      this.$axios({
        url: "/api/orders?action=trade_view",
        params: params
      }).then(res => {
        if (res.state === 1) {
          this.totalSubTable = Number(res.total);
          res.root.forEach(val => {
            for (let keyName in val) {
              val[keyName] = val[keyName] == null ? "" : val[keyName];
              if (typeof val[keyName] === "boolean") {
                val[keyName] = val[keyName] == true ? "" : "";
              }
              if (keyName === "status") {
                for (let i in this.statusList) {
                  if (val[keyName] == this.statusList[i].value) {
                    val.status_name = this.statusList[i].label;
                  }
                }
              }
            }
          });
          this.subTable = res.root;
          return;
        }
        alert(res.errmsg);
      });
      if (bool) { //  
          this.fail_id_arr = []
          for (let j = 0, len2 = this.subTable.length; j < len2; j++) {
              document.getElementsByClassName('el-table__row')[j].setAttribute('title','')
          }
      }
    }
  }
};
</script>

